import React, { useState } from "react";
import axios from "axios";

const AddFridgeItemForm = ({ setItems }) => {
  const [name, setName] = useState("");
  const [quantity, setQuantity] = useState(1);
  const [unit, setUnit] = useState("ÂÄã");

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post("http://127.0.0.1:8000/fridge_items/", {
        name,
        quantity: parseInt(quantity),
        unit
      });

      setItems((prevItems) => {
        const updatedItems = prevItems.map((item) =>
          item.name === response.data.name ? response.data : item
        );

        // „ÇÇ„ÅóÊñ∞„Åó„ÅÑÈ£üÊùê„Å™„Çâ„É™„Çπ„Éà„Å´ËøΩÂä†
        if (!prevItems.some((item) => item.name === response.data.name)) {
          return [...updatedItems, response.data];
        }

        return updatedItems;
      });

      // ÂÖ•ÂäõÊ¨Ñ„Çí„É™„Çª„ÉÉ„Éà
      setName("");
      setQuantity(1);
      setUnit("ÂÄã");
    } catch (error) {
      console.error("Error adding item:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="add-item-form">
      <input
        type="text"
        value={name}
        onChange={(e) => setName(e.target.value)}
        placeholder="È£üÊùêÂêç"
        required
      />
      <input
        type="number"
        value={quantity}
        onChange={(e) => setQuantity(e.target.value)}
        min="1"
        required
      />
      <select value={unit} onChange={(e) => setUnit(e.target.value)}>
        <option value="ÂÄã">ÂÄã</option>
        <option value="Êú¨">Êú¨</option>
        <option value="g">g</option>
      </select>
      <button type="submit">ËøΩÂä†</button>
    </form>
  );
};

export default AddFridgeItemForm;
